#app {
    height: 100%;
}

html,
body {
    position: relative;
    height: auto;
}

ul,
li {
    list-style: none;
}

body {
    background: #fff;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    top: 0;
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
    border-top: 2px solid #394177;
}

.start {
    transform: scale(0);

}

.end {
    transform: scale(1);
    transition: all 0.5s;
}

/*헤더 네비*/
.nav {
    width: 1500px;
    height: 118px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

.nav-link>img {
    width: 106px;
    height: 37px;
    cursor: pointer;
}

.nav-link>.nav-item:nth-of-type(1) {
    margin: 0 0 0 10px;
}

.nav-link>.nav-item {
    font-size: 16px;
    color: #535353;
    font-weight: bold;
    border-bottom: 2px solid #fff;
    margin: 0 7px;
    cursor: pointer;
}

.nav-link>.nav-item:hover {
    color: #394177;
    border-bottom: 2px solid #394177;
}

.menuicon {
    width: 26px;
    margin: 0 0 5px 10px;
    cursor: pointer;
}

.iconbox {
    margin: 0 0 0 90px;
    display: flex;
}

.iconbox>.nav-link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 -7px;
}

.iconbox>.nav-link>span {
    font-size: 11px;
    color: #000;
    margin: 5px 0;
}

.iconbox>.nav-link>svg {
    color: #535353;
    cursor: pointer;
    width: 20px;
}

.iconbox>.nav-link>svg:hover {
    color: #394177;
}

/*이미지 슬라이더*/
.carousel {
