}

.slick-arrow.slick-next {
    float: right;
    width: 34px;
    height: 34px;
    margin: -80px 0 0 0;
}

.slick-arrow.slick-prev {
    float: left;
    margin: -80px 0 0 0;
}

.slick-arrow.slick-next::before {
    color: #6d6d6d;
    font-size: 50px;
    content: ">";
}

.slick-arrow.slick-prev::before {
    color: #6d6d6d;
    font-size: 50px;
    content: "<";
}

.slick-track {
    height: 100%;
